<!--
    This component is the main form for token deployment.
    Allows you to select the type of token and enter parameters for its creation.

    components:
        AbiForm,
        ContractList

    props:
        descriptions: Array,
        contract: Object
        deployDlg: Boolean
-->

<template>

    <v-card>
        <v-card-title v-if="contract">{{ contract.description.name }}</v-card-title>
        <v-card-title v-else>Contract</v-card-title>
        <v-card-subtitle align="left" class="description">
            <p v-if="contract">Type: {{ contract.description.type }}</p>
            <p v-else>Type: Unknown</p>
            <p v-if="contract">{{ contract.description.description }}</p>
            <p v-else>Please select contract to deploy.</p>
        </v-card-subtitle>
        <div class="description-border"></div>

        <v-card-text>
            <v-container>
                <v-row class="left-list">
                    <v-col class="list-border" sm="3" xs="12">
                        <contract-list :descriptions="descriptions" :contract="contract" @contractSelected="setContract" />
                    </v-col>
                    <v-col sm="9" xs="12">
                        <v-row>
                            <v-col>
                                <abi-form v-if="inputs.length" :inputs="inputs" ref="gForm"></abi-form>
                            </v-col>
                        </v-row>
                        <v-row>
                            <v-col class="form-buttons">
                                <v-btn :disabled="!wallet || !contract" color="primary" @click="deploy" class="mr-4 mt-4">
                                    <v-progress-linear v-if="deployDlg" indeterminate rounded height="7" color="warning"></v-progress-linear>
                                    <span v-else>Deploy</span>
                                </v-btn>
                                <v-btn color="error" class="mr-4 mt-4" @click="reset">Reset</v-btn>
                            </v-col>
                        </v-row>
                    </v-col>
                </v-row>
            </v-container>
        </v-card-text>
    </v-card>

</template>

<script>

    import ContractList from '@/components/deploy/ContractList';
    import { mapState, mapActions } from 'vuex';
    const AbiForm = () => import('@/components/AbiForm');

    export default {
        name: 'DeployForm',

        components: {
            'abi-form': AbiForm,
            'contract-list': ContractList
        },

        props: {
            descriptions: Array,
            contract: Object,
            deployDlg: Boolean
        },

        data () {
            return {
                inputs: []
            }
        },

        computed: {
            ...mapState({
                'wallet': state => state.chain.wallet,
                'w3': state => state.chain.w3
            })
        },

        watch: {
            contract(val) {
                const constructor = val.raw.abi.filter( obj => obj.type === 'constructor' )[0];
                let ins = [];

                if (constructor) {
                    ins = constructor.inputs;
                }

                this.inputs = [];

                this.inputs.push({
                    name: 'localName',
                    type: 'String'
                });

                this.inputs.push(...ins);
            }
        },

        methods: {

            ...mapActions({
                'displayAlert': 'alert/displayAlert'
            }),

            reset () {
                this.$refs.gForm.reset();
            },

            deploy() {
                if (!this.$refs.gForm.validate()) return;

                const ts = this.$refs.gForm.form.filter( obj => obj.name === 'totalSupply_' )[0];
                const dec = this.$refs.gForm.form.filter( obj => obj.name === 'decimals_' )[0];

                if (ts && dec) {
                    ts.value += '0'.repeat(dec.value);
                }

                this.$refs.gForm.form.map( input => {
                    if (input.type.startsWith('bytes')) {
                        if (input.type.endsWith('[]')) {
                            const tmpInputs = [];

                            input.value.map( val => {
                                tmpInputs.push(this.w3.utils.utf8ToHex(val));
                            })

                            input.value = tmpInputs;
                        } else {
                            input.vlaue = this.w3.utils.utf8ToHex(input.value);
                        }
                    }
                });

                this.$emit('formSubmitted', this.$refs.gForm.form);
            },

            setContract(name) {
                this.$emit('contractSelected', name);
            },
        }
    }

</script>

<style scoped>

    .v-card {
        min-width: 600px;
        width: 800px;
    }

    .v-card__text {
        padding: 0px;
    }

    .col {
        flex-basis: 100%;
    }

    .list-border {
        margin: 10px 0px 10px 0px;
        border-style: none solid none none;
        border-width: 1px;
    }

    @media only screen and (max-width: 600px) {
        .v-card {
            min-width: 300px;
            width: 100%;
        }

        .list-border {
            margin: 0px 10px 0px 10px;
            border-style: none none solid none;
            border-width: 1px;
        }

        .row {
            margin-top: 0px;
        }

        .v-list {
            padding: 0px;
        }
    }

    .description {
        padding-left: 25px;
    }

    .v-card__subtitle {
        padding-bottom: 0px;
    }

    .description-border {
        width: 96%;
        margin-left: 2%;
        border-style: none none solid none;
        border-width: 1px;
    }

    .form-buttons {
        display: flex;
        justify-content: right;
    }

</style>
