<!--
    This component serves to list all types of contacts.
    Depending on the screen size, contracts will be displayed as a virtual scroll or as a drop down list.

    props:
        descriptions: Array,
        contract: Object

-->

<template>

    <div>
        <v-subheader>Contracts</v-subheader>

        <v-virtual-scroll :items="descriptions" :bench="5" :item-height="50" height="inherit" min-height="300" max-height="600" class="full-list hidden-xs-only">
            <template v-slot:default="{ item }">
                <v-list-item :key="item.name" @click="selected(item.name)">
                    <v-list-item-content :class="item.name == contractName ? 'active' : ''">{{ item.name }}</v-list-item-content>
                </v-list-item>
            </template>
        </v-virtual-scroll>

        <div class="compact-list hidden-sm-and-up">
            <v-tooltip color="info" bottom>
                <template v-slot:activator="{ on, attrs }">
                    <div v-bind="attrs" v-on="on">
                        <v-select
                            v-model="selectedContract"
                            :items="descriptions"
                            item-text="name"
                            item-value="name"
                            label="Contract"
                            single-line
                            dense
                        ></v-select>
                    </div>
                </template>
                <span>Select contract</span>
            </v-tooltip>
        </div>
    </div>

</template>

<script>

    export default {
        name: 'ContractList',

        props: {
            descriptions: Array,
            contract: Object
        },

        data() {
            return {
                selectedContract: ''
            }
        },

        computed: {
            contractName() {
                if (this.contract) {
                    return this.contract.description.name;
                } else {
                    return '';
                }
            }
        },

        watch: {
            selectedContract(val) {
                this.selectedContract = val;
                this.selected(val);
            }
        },

        methods: {
            selected(selectedName) {
                this.selectedContract = selectedName;
                this.$emit('contractSelected', selectedName);
            }
        }

    }

</script>

<style scoped>

    .full-list {
        min-width: fit-content;
    }

    .compact-list {
        padding-left: 25px;
        padding-right: 25px;
    }

    .v-list-item__content {
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
        max-width: 155px;
        display: block;
    }

    .active {
        box-shadow: var(--v-accent-base) 5px 0px 0px inset !important;
        padding-left: 10px;
        color: var(--v-primary-base) !important;
    }

</style>
