<!--
    This component is used as a basic component for deployment
    url: http://localhost:8080/#/deploy/:type

     components:
        DeployForm
-->

<template>
    <div>

        <generic-dlg v-if="progressDlg" :show="progressDlg" persistent>
            <v-card>
                <v-card-title></v-card-title>
                <v-card-subtitle>
                    <p>{{ dialogMessage }}</p>
                    <p>This may take a few minutes.</p>
                    <p><strong>Do not close or refresh this page while deploying!</strong></p>
                </v-card-subtitle>
                <v-card-text>
                    <v-progress-circular :size="70" :width="7" color="primary" indeterminate></v-progress-circular>
                </v-card-text>
            </v-card>
        </generic-dlg>

        <generic-dlg v-if="successDlg" :show="successDlg" @done="successDlg = false">
            <v-card>
                <v-card-title>
                    <span>Success</span>
                    <v-icon class="pl-2" color="primary">mdi-check-circle</v-icon>
                </v-card-title>
                <v-card-subtitle align="left">Contract deployed</v-card-subtitle>
                <v-card-text>
                    <p>Your contract has been deployed at <a :href="`${this.chainExplorerURL}address/${this.contractAddress}`" target="_blank">{{ this.contractAddress }}</a></p>
                    <p>Note that it might take 30 - 90 sceonds for the block to propagate befor it's visible in chain explorer.</p>
                </v-card-text>
                <v-card-actions class="justify-end">
                    <v-btn color="primary" text @click="$router.push({ name: 'Manager', query: { address: contractAddress } });" rounded>View in manager</v-btn>
                    <v-btn color="error" text @click="successDlg = false" rounded>Close</v-btn>
                </v-card-actions>
            </v-card>
        </generic-dlg>

        <v-container>
            <v-row class="justify-center">
                <deploy-form :descriptions="filteredDescriptions" :contract="contract" :deployDlg="progressDlg" @contractSelected="setContract" @formSubmitted="deploy" ref="dForm" class="ml-1 mb-1" />
            </v-row>
        </v-container>

    </div>
</template>

<script>

    import DeployForm from '@/components/deploy/DeployForm';
    import { doc, getDoc } from 'firebase/firestore';
    import { mapState, mapActions } from 'vuex';
    import { endpoints } from '@/store/constants';
    const GenericDlg = () => import('@/components/GenericDlg');

    export default {
        name: 'Deploy',

        components: {
            'deploy-form': DeployForm,
            'generic-dlg': GenericDlg
        },

        data() {
            return {
                contract: null,
                formData: null,
                chains: [],

                progressDlg: false,
                dialogMessage: '',

                successDlg: false,
                contractAddress: '',
                chainExplorerURL: '',
            }
        },

        computed: {
            ...mapState({
                'w3': state => state.chain.w3,
                'wallet': state => state.chain.wallet,
                'currentChain': state => state.chain.currentChain,
                'db': state => state.db.db,
                'descriptions': state => state.db.descriptions
            }),

            filteredDescriptions() {
                if (this.descriptions) {
                    return this.descriptions.filter( d => d.type == this.$route.params.type );
                }

                return [];
            }
        },

        mounted() {
            this.chains = endpoints;
        },

        methods: {
            ...mapActions({
                'addUserContract': 'db/addUserContract',
                'displayAlert': 'alert/displayAlert'
            }),

            async setContract(name) {
                const rawContract = await this.getContract(name);
                const descContract = this.descriptions.filter( dsc => dsc.name == name )[0];

                this.contract = {
                    raw: rawContract,
                    description: descContract
                }
            },

            async getContract(name) {
                try {
                    const code = await getDoc(doc(this.db, 'code', name));
                    const abi = await getDoc(doc(this.db, 'abi', name));

                    if (code.exists() && abi.exists()) {
                        return {
                            bytecode: code.data().bytecode,
                            source: code.data().source,
                            compilerVersion: code.data().compilerVersion,
                            evmVersion: code.data().evmVersion,
                            abi: abi.data().abi
                        };
                    } else {
                        this.displayAlert({ message: 'Error fetching compiled contract', type: 'error' });
                    }

                } catch (err) {
                    this.displayAlert({ message: err.code, type: 'error' });
                }
            },

            async deploy(emittedData) {
                this.formData = emittedData;

                try {
                    this.progressDlg = true;
                    this.dialogMessage = 'Deploying contract...';

                    const ethContract = new this.w3.eth.Contract(this.contract.raw.abi);

                    const constructor = this.contract.raw.abi.filter( obj => obj.type === 'constructor' )[0];
                    let constructorInputs = [];
                    
                    if (constructor) {
                        constructorInputs = constructor.inputs;
                    }
                    
                    const constructorArgumentTypes = [];
                    const constructorArguments = [];

                    constructorInputs.map( i => {
                        constructorArguments.push(this.formData.filter( d => d.name === i.name)[0].value);
                    });

                    constructorInputs.map( i => {
                        constructorArgumentTypes.push(i.type);
                    });

                    this.contract.raw['encodedConstructorArguments'] = this.w3.eth.abi.encodeParameters(constructorArgumentTypes, constructorArguments);

                    const obj = {
                        data: this.contract.raw.bytecode,
                        arguments: constructorArguments
                    };

                    ethContract.deploy(obj).send({
                        from: this.wallet,
                    }).on('transactionHash', async tHash => {
                        while (true) {
                            let receipt = await this.w3.eth.getTransactionReceipt(tHash);

                            if (receipt && receipt.contractAddress) {
                                this.contractAddress = receipt.contractAddress;

                                this.contractAddress = this.contractAddress.toLowerCase();

                                const userContract = {
                                    wallet: this.wallet,
                                    address: this.contractAddress,
                                    name: this.formData.filter( d => d.name === 'localName' )[0].value,
                                    abi: this.contract.description.name,
                                    chain: this.currentChain,
                                    constructorArguements: this.contract.raw.encodedConstructorArguments,
                                    verified: false
                                };

                                this.addUserContract(userContract);

                                this.verifyOnBlockExplorer(userContract);

                                this.$refs.dForm.reset();
                                break;
                            }

                            this.dialogMessage = 'Waiting for a mined block to include your contract...';
                            await this.sleep(2000);
                        }
                    }).catch( err => {
                        this.progressDlg = false;
                        this.displayAlert({ message: err.message, type: 'error' });
                    });

                } catch (err) {
                    this.progressDlg = false;
                    this.displayAlert({ message: err.message, type: 'error' });
                }
            },

            sleep(ms) {
                return new Promise(resolve => setTimeout(resolve, ms));
            },

            async verifyOnBlockExplorer(userContract) {
                this.dialogMessage = 'Verifying contract...';
                await this.sleep(30000);

                const chain = this.chains.filter( el => el.id === userContract.chain )[0];
                while (!chain) { await this.sleep(100); }
                this.chainExplorerURL = chain.blockExplorerUrls[0];

                fetch(chain.blockExplorerAPIUrls[0], {
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
                    },
                    body: new URLSearchParams({
                        apikey: chain.blockExplorerAPIKey,
                        contractaddress: this.contractAddress,
                        contractname: userContract.abi,
                        optimizationUsed: 1,
                        runs: 200,
                        licenseType: 3,
                        module: 'contract',
                        action: 'verifysourcecode',
                        codeformat: 'solidity-single-file',
                        sourceCode: this.contract.raw.source,
                        compilerversion: 'v' + this.contract.raw.compilerVersion,
                        evmversion: this.contract.raw.evmVersion,
                        constructorArguements: this.contract.raw.encodedConstructorArguments.slice(2)
                    })
                }).then( obj => obj.json() ).then( async res => {
                    if (res.status == '1') {

                        const url = new URL(chain.blockExplorerAPIUrls[0]);
                        url.search = new URLSearchParams({
                            apikey: chain.blockExplorerAPIKey,
                            guid: res.result,
                            module: 'contract',
                            action: 'checkverifystatus'
                        });

                        let done = false;

                        while (!done) {
                            try {
                                const objV = await fetch(url);
                                const resV = await objV.json();

                                if (resV.status == '1') {
                                    this.progressDlg = false;
                                    this.successDlg = true;
                                    done = true;

                                    userContract.verified = true;
                                    this.addUserContract(userContract);
                                } else {
                                    if (resV.result === 'Pending in queue') {
                                        await this.sleep(30000);
                                    } else if (resV.result === 'Already Verified') {
                                        this.progressDlg = false;
                                        this.successDlg = true;
                                        done = true;

                                        userContract.verified = true;
                                        this.addUserContract(userContract);
                                    } else {
                                        this.progressDlg = false;
                                        this.successDlg = true;
                                        done = true;

                                        this.displayAlert({ message: 'Contract deployed but not verified. Reason: ' + resV.result, type: 'error' });
                                    }
                                }
                            } catch(err) {
                                this.progressDlg = false;
                                this.successDlg = true;
                                done = true;

                                this.displayAlert({ message: 'Contract deployed but not verified. Reason: ' + err.message, type: 'error' });
                            }
                        }

                    } else {
                        this.progressDlg = false;
                        this.successDlg = true;
                        this.displayAlert({ message: res.result, type: 'error' });
                    }
                }).catch( err => {
                    this.progressDlg = false;
                    this.successDlg = true;
                    this.displayAlert({ message: err.message, type: 'error' });
                });
            }
        }

    }

</script>

<style scoped>

    .layout {
        justify-content: center;
    }

</style>
